html {
    height: 100%;
}

body {    
    margin: 0;
    padding: 0;
    
    height: 100%;
}

.home-root {
    display: flex;
    flex-direction: column;

    height: 100%;

    .canvas {
        width: 100%;
        height: 60%;

        touch-action: none;
    }

    .editor {
        display: flex;
        flex-direction: column;

        color: lightgray;
        background-color: rgb(34, 34, 34);

        h4 {
            display: flex;
            flex-direction: column;
            justify-content: center;

            margin: 0;

            height: 30px;

            padding-left: 20px;
            border-style: dashed;
            border-width: 2px;

            background-color: transparent;
        }
    }
}